import React, {Component, PropTypes} from "react";
import LabeledInput from "eshop/modules/core/components/hoc/LabeledInput";
import {connect} from "react-redux";
import {getCustomerDataForm} from "../../selectors";
import {changeCustomerDataFormField} from "../../actions/app";
import {requestCartCustomerData} from "../../actions/data";
import {isFieldDisabled, isFieldDisabledForValidation} from "../../utils/utils";

export class MulticartCustomerPersonalDetailsView extends Component {

    getPropsForInput(name) {
        return {
            name,
            defaultValue: this.props[name] || "",
            errors: this.props.errors && this.props.errors[name],
            onBlur: this.props.changeCustomerDataFormField
        };
    }

    getInputValidationParams(name) {
        const validationDisabled = isFieldDisabledForValidation(this.props, name);
        const disabled = isFieldDisabled(this.props, name);
        let params = {
            disabled
        };
        const valid = (this.props.errors === undefined && this.props[name] !== null && this.props[name].length > 0) ? true : this.props.errors && this.props.errors[name] && (this.props.errors[name].length === 0);
        if(!disabled || (!validationDisabled && !valid)) {
            params.valid = valid;
        }
        return params;
    }

    componentDidMount() {
        this.props.requestCartCustomerData();
    }

    render() {
        return (
            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-no-padding">
                <LabeledInput key="firstName" {...this.getPropsForInput("firstName")} label={this.props.descriptions.firstName} {...this.getInputValidationParams("firstName")} labelType="floating" maxLength="255" virtualKeyboard={true}/>

                <LabeledInput key="lastName" {...this.getPropsForInput("lastName")} label={this.props.descriptions.lastName} {...this.getInputValidationParams("lastName")} labelType="floating" className="u-spacing-top-l" maxLength="255" virtualKeyboard={true}/>

            </div>
        )
    }
}

MulticartCustomerPersonalDetailsView.propTypes = {
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    readOnly: PropTypes.bool,
    requestCartCustomerData: PropTypes.func,
    changeCustomerDataFormField: PropTypes.func
};

const MulticartCustomerPersonalDetailsDataComponent = connect(
        getCustomerDataForm,
        {changeCustomerDataFormField, requestCartCustomerData}
    )(MulticartCustomerPersonalDetailsView);
export default MulticartCustomerPersonalDetailsDataComponent;
